/* Reset some basic elements */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Set a background color for the body and use a legible font */
  body {
    font-family: 'Arial', sans-serif;
    background-color: #f4f4f4;
    color: #333;
  }
  
  /* Style the main container */
  .App {
    display: flex;
    height: 100vh;
  }
  
  /* Style for sidebar menu */
  .sidebar {
    background: #333;
    color: #fff;
    width: 250px;
    padding: 20px;
    display: flex;
    flex-direction: column;
  }
  
  .sidebar-header {
    font-size: 3em; /* Increased font size */
    margin-bottom: 20px;
  }
  
  .nav-menu {
    list-style: none;
    flex-grow: 1;
  }
  
  .nav-menu a {
    color: #fff;
    text-decoration: none;
    padding: 20px 30px; /* Increased padding */
    display: block;
    border-radius: 4px;
    margin-bottom: 10px;
    transition: background 0.3s;
    font-size: 1.6em; /* Increased font size */
  }
  
  .nav-menu a:hover {
    background: #575757;
  }
  
  /* Style for main content area */
  .content {
    flex-grow: 1;
    padding: 20px;
    background: #fff;
    overflow-y: auto;
  }
  
  /* Typography */
  h1 {
    margin-bottom: 20px;
    font-size: 4em; /* Increased font size */
  }
  
  p {
    margin-bottom: 20px;
    font-size: 2em; /* Increased font size */
  }
  
  /* Specific styles for images */
  img {
    max-width: 100%;
    height: auto;
    border-radius: 4px;
  }
  
  /* Specific styles for product information */
  .product-info {
    font-size: 2em; /* Increased font size */
  }
  